import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { ShoppingListService } from  '../shopping-list.service';
import {Ingredient} from '../ingredient.model';

@Component({
  selector: 'app-ingredient-entry',
  templateUrl: './ingredient-entry.component.html',
  styleUrls: ['./ingredient-entry.component.css']
})
export class IngredientEntryComponent implements OnInit {

  constructor(private shoppingListService:ShoppingListService) { }

  @ViewChild('IngredientNameInput',{static:true}) ingredientName:ElementRef;
  @ViewChild('IngredientAmountInput',{static:true}) ingredientAmount:ElementRef;


  ngOnInit(): void {
   }
   
   onAdd() {
   	let ingredientName = this.ingredientName.nativeElement.value;
   	let ingredientAmount = this.ingredientAmount.nativeElement.value;
   	this.shoppingListService.addIngredient(new Ingredient(ingredientName,ingredientAmount));
   }


}
